@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');
.instruction-main-div {
  padding: 30px 30px;
}

.instructions-heading .MuiTypography-body1 {
  font-size: 40px;
  color: #04724d;
  font-weight: bold;
  /* font-family: Ubuntu; */
  line-height: 1.2;
}
.instructions-subHeading .MuiTypography-body1 {
  font-size: 25px;
  color: #000000;
  opacity: 60%;
}

.instructions-logo{
  width: 150px;
}
.instructions-logo img{
  width: 100%;
}

.main-wrapper{
  display: flex;
  justify-content: space-between;
}

/* .page-content {
  padding: 0 150px;
  margin-top: 30px;
} */

.mainstepper-mobile{
  display: none;
}
.button-new{
  display: none;
}
.page-content .MuiPaper-root {
  background: transparent;
}

.page-content .MuiStepLabel-label.MuiStepLabel-alternativeLabel {
  display: none;
}

.page-content .MuiStepConnector-alternativeLabel {
  display: none;
}

.mainstepper {
  /* width: 100%; */
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.page-content .MuiSvgIcon-root {
  width: 2em;
  height: 2em;
}

 .Button {
  background-color: #04724d !important; 
  color: #ffffff !important;
  width: 150px;
  border-radius: 15px;
  display: flex;
  justify-content: space-between;
  font-size: 13px !important;
 }
 .Button1 {
  font-size: 13px !important;
  background-color: #04724d !important;
  color: #ffffff !important;
  width: 150px;
  border-radius: 15px;

}

 .MuiButton-contained{
  border-radius: 4px;
}

.page-content .MuiButton-contained:hover {
  background-color: #04724d;
}

.page-content .MuiStepIcon-root.MuiStepIcon-active {
  color: #04724d;
}

.page-content .MuiStepIcon-root.MuiStepIcon-completed {
  cursor: pointer;
  color: #04724d;
}
.page-content .MuiStepIcon-root {
  color: #04724d1a;
}

/* .Button {
  background-color: #04724D;
  color: #FFFFFF;
  
} */

.contents {
  margin-top: 20px;
  padding: 50px;
  border-radius: 30px;
  background-color: #ffffff;
}

.li {
  font-size: 24px;
  margin-bottom: 40px;
  color: #000000 !important;
  opacity: 1;
  font-weight: 500;
}

.arrowChanges {
  width: 40px !important;
  height: 27px !important;
  float: right;
}
.buttonNextFlex {
  display: flex;
  justify-content: space-between;
}


@media (max-width: 1600px) {

  .instructions-heading .MuiTypography-body1 {
    font-size: 40px;
    line-height: 1;
  }
  .instructions-subHeading .MuiTypography-body1 {
    font-size: 21px;
  }
  /* .page-content {
    padding: 0 100px;
    margin-top: 0px;
  } */
  .li {
    /* font-size: 27px; */
    margin-bottom: 25px;
  }

}

@media (max-width: 1440px) {

  /* .page-content {
    padding: 0 90px;
  } */
  .contents {
    margin-top: 5px;
    padding: 35px;
  }
  .instructions-heading .MuiTypography-body1 {
    font-size: 40px;
    line-height: 1.3;
  }
  .instructions-subHeading .MuiTypography-body1 {
    font-size: 17px;
  }

  .page-content .MuiStepIcon-text {
    font-size: 0.5rem;
  }
  .page-content .MuiButton-contained {
    width: 180px;
  }
}

@media (max-width: 1366px) {
  .instructions-heading .MuiTypography-body1 {
    font-size: 30px;
  }



}

@media (max-width: 1024px) {
  .instruction-main-div{
    padding: 30px 20px 0 20px;
  }
  .Button,.Button1{
    font-size: 13px !important;
  }

  .instructions-heading .MuiTypography-body1 {
    font-size: 18px;
  }
  .instructions-subHeading .MuiTypography-body1 {
    font-size: 11px;
    opacity: 100%;
    padding: 10px 0;
  }
  .instructions-logo{
    width: 100px;
  }

  /* .page-content {
    padding: 0 45px;
  } */
  .li {
    font-size: 12px;
    margin-bottom: 15px;
  }

  .mainstepper{
    display: none;
  }
  .mainstepper-mobile{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0  auto;
  }
  .button-new{
    display: flex;
    position: fixed;
    bottom: 0;
    padding: 10px;
    width: 100%;
    background: #fff;
    justify-content: flex-end;
  }
  .page-content .MuiButton-contained {
    width: 120px;
    font-size: 10px;
    padding: 6px;
  }

  .page-content .MuiStepper-root {
    padding: 15px 0 15px 0;
  }
  .contents {
    padding: 13px;
    margin-top: 12px;
  }


}



